.menu {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    @include flex-col-center-center;
    justify-content: start;
    display: none;
    opacity: 0;
    width: 100%;
    height: 100%;
    padding: 30vw 0;
    overflow-y: scroll;
    ul {
        li {
            padding: 0 calc(24vw / $str-wid-m);
            border: 1px solid $white;
            border-radius: 10px;
            margin-bottom: calc(20vw / $str-wid-m);
            a {
                font-size: calc(16vw / $str-wid-m);
                line-height: 3;
                text-decoration: none;
                color: $white;
                img {
                    margin-left: calc(22vw / $str-wid-m);
                    width: calc(32vw / $str-wid-m);
                }
            }
        }
    }
    p {
        display: none;
        text-align: center;
        color: $white;
        font-size: calc(14vw / $str-wid-m);
        img {
            width: calc(20vw / $str-wid-m);
            margin: 0 2vw;
        }
    }
    &--trigger {
        z-index: 999;
        position: absolute;
        top: calc(10vw / $str-wid-m);
        right: calc(10vw / $str-wid-m);
        width: calc(40vw / $str-wid-m);
        height: calc(40vw / $str-wid-m);
        span {
            &::after,
            &::before {
                @include pseudo;
                width: 100%;
                height: 100%;
                transition-duration: 0.5s;
                transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
            }
            &::before {
                background: url(../../img/menu/menu.svg) no-repeat center/100%;
            }
            &::after {
                opacity: 0;
                background: url(../../img/menu/menu-active.svg) no-repeat center/100%;
            }
        }
        &::after,
        &::before {
            z-index: 9999;
            @include pseudo;
            width: 40%;
            top: 50%;
            left: 30%;
            border-radius: 10px;
            height: calc(2vw / $str-wid-m);
            background-color: $white;
            transition: 1s;
        }
        &::after {
            transform: translateY(200%);
        }
        &::before {
            transform: translateY(-200%);
        }
        &.active {
            span {
                &::before {
                    animation: menuBtn 1s forwards;
                }
                &::after {
                    opacity: 1;
                }
            }
            &::after,
            &::before {
                background-color: $red;
            }
            &::after {
                transform: translateY(0%) rotate(-45deg);
            }
            &::before {
                transform: translateY(0%) rotate(45deg);
            }
        }
        &.unactive {
            span {
                &::before {
                    animation: menuBtnReverse 1s forwards;
                }
            }
        }
    }
}

@media (min-width: 750px) {
    .menu {
        padding: 50px;
        ul {
            li {
                padding: 0 12px;
                border: 1px solid $white;
                border-radius: 10px;
                margin-bottom: 10px;
                a {
                    font-size: 16px;
                    letter-spacing: 8px;
                    img {
                        margin-left: 20px;
                        width: 25px;
                    }
                }
            }
        }
        div {
            img {
                width: 80%;
            }
        }
        p {
            display: block;
            font-size: 16px;
            margin: 20px 0;
            img {
                width: 30px;
                margin: 0 10px;
            }
        }
        &--trigger {
            top: 20px;
            right: 20px;
            width: 80px;
            height: 80px;
            &::after,
            &::before {
                width: 40%;
                top: 50%;
                left: 30%;
                border-radius: 10px;
                height: 3px;
            }
            &::after {
                transform: translateY(200%);
            }
            &::before {
                transform: translateY(-200%);
            }
            &.active {
                &::after,
                &::before {
                    background-color: $red;
                }
                &::after {
                    transform: translateY(0%) rotate(-45deg);
                }
                &::before {
                    transform: translateY(0%) rotate(45deg);
                }
            }
        }
    }
}

@media (min-width: 1200px) {
    .menu {
        display: flex !important;
        flex-direction: column-reverse;
        opacity: 1 !important;
        left: unset;
        right: 0;
        width: 30%;
        padding: 50px;
        background-color: $yellow;
        &--trigger {
            display: none;
        }
        ul {
            li {
                padding: 0 24px;
                border: 1px solid $white;
                border-radius: 10px;
                margin-bottom: 20px;
                background-color: $red;
                a {
                    font-size: 16px;
                    letter-spacing: 8px;
                    img {
                        margin-left: 20px;
                        width: 25px;
                    }
                }
            }
        }
        div {
            img {
                width: 80%;
            }
        }
        p {
            font-size: 16px;
            color: $blue;
            margin: 20px 0;
            img {
                width: 30px;
                margin: 0 10px;
            }
        }
    }
}
