.carousel {
  &__container {
    position: relative;
    background-color: $black;
    width: 100%;
    padding: 0 10vw 18vw;
    overflow: hidden;
    z-index: 9;
    &::after {
      display: block;
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: calc(100vw / $str-wid-m);
      background: url(../../img/house-shadow.png) no-repeat center / 100% 100%;
    }
    img {
      border-radius: 10px;
    }
    .slide {
      padding-top: 25vw;
      &::before {
        z-index: -1;
        position: absolute;
        left: 50%;
        top: 0;
        display: block;
        content: "";
        width: calc(55vw / $str-wid-m);
        height: calc(83vw / $str-wid-m);
        background: url(../../img/carousel/pin.png) no-repeat center / 100% 100%;
        transform: translate(-50%, -0%);
      }
    }
    .carousel.carousel-slider {
      overflow: visible;
      .control-arrow {
        position: absolute;
        top: calc(50% + 12.5vw);
        @include flex-col-center-center;
        width: calc(50vw / $str-wid-m);
        height: calc(50vw / $str-wid-m);
        border: 1px solid $red;
        border-radius: 100%;
        padding: 0;
        background-color: $white;
        transform: translate(80%, -50%);
        &:hover {
          background-color: $white;
        }
        &::after {
          display: block;
          content: "";
          border: 0;
          width: calc(8vw / $str-wid-m);
          height: calc(1.5vw / $str-wid-m);
          background-color: $red;
          border-radius: 10px;
          transform: rotate(135deg) translateY(-3vw / $str-wid-m);
        }
        &::before {
          content: "";
          display: block;
          border: 0;
          width: calc(8vw / $str-wid-m);
          height: calc(1.5vw / $str-wid-m);
          background-color: $red;
          border-radius: 10px;
          transform: rotate(45deg) translateY(-3vw / $str-wid-m);
        }
      }
      .control-prev {
        transform: translate(-80%, -50%);
        &::after {
          display: block;
          content: "";
          border: 0;
          width: calc(8vw / $str-wid-m);
          height: calc(1.5vw / $str-wid-m);
          background-color: $red;
          transform: rotate(315deg) translateY(-5vw / $str-wid-m);
        }
        &::before {
          content: "";
          display: block;
          border: 0;
          width: calc(8vw / $str-wid-m);
          height: calc(1.5vw / $str-wid-m);
          background-color: $red;
          transform: rotate(225deg) translateY(-5vw / $str-wid-m);
        }
      }
    }
  }
  &__desc {
    @include flex-row-center-center;
    position: absolute;
    bottom: 8vw;
    left: 0;
    width: 100%;
    font-size: $content-primary;
    letter-spacing: 10px;
    color: $white;
    z-index: 99;
  }
}
@media (min-width: 750px) {
  .carousel {
    &__container {
      padding: 0;
      padding-bottom: 80px;
      background-color: $yellow;
      &::after {
        background: none;
      }
      .slide {
        padding-top: 25px;
        div {
          padding: 50px;
          img {
            border: 2px solid $red;
          }
        }
        &::before {
          width: 58px;
          height: 77px;
        }
      }
      .carousel.carousel-slider {
        .control-arrow {
          top: 50%;
          width: 64px;
          height: 64px;
          transform: translate(-200px, -50%);
          &::after {
            width: 25px;
            height: 6px;
            transform: rotate(135deg) translateY(-5px);
          }
          &::before {
            width: 25px;
            height: 6px;
            transform: rotate(45deg) translateY(-5px);
          }
        }
        .control-prev {
          transform: translate(200px, -50%);
          &::after {
            width: 25px;
            height: 6px;
            transform: rotate(315deg) translateY(-14px);
          }
          &::before {
            width: 25px;
            height: 6px;
            transform: rotate(225deg) translateY(-14px);
          }
        }
      }
    }
    &__desc {
      font-size: 40px;
      bottom: 0px;
      // left: 20px;
      background-color: $red;
    }
  }
}
